import React, { useState } from 'react'
import { useNavigate, NavLink } from 'react-router-dom'
import "./signup.css"
import img1 from "./Email icon.svg"
import img2 from "./Frame.svg"
import img3 from "./Password icon.svg"
import img4 from "./User name icon.svg"

function Signup() {


    const [user, setuser] = useState({
        fullName: "",
        pass: "",
        email: "",
    })



    const getUserData = (e) => {
        setuser({ ...user, [e.target.name]: e.target.value })
    }


    const history = useNavigate();


    const storingData = () => {
        history("/home")
    }

    return (
        <div>
            <div className="containersignup">
                <div className="first">
                    <h1>SAYLANI WELFARE</h1>
                    <p>ONLINE DISCOUNT STORE</p>
                </div>
                <form>
                    <div className="mb-3 input">
                        <input type="text" className="form-control" id="fullName"

                            name='fullName'
                            value={user.email}
                            onChange={getUserData}
                            placeholder="Full Name"
                        />
                        <img src={img4} alt="" />
                    </div>
                    <div className="mb-3 input">
                        <input type="number" className="form-control" id="contact"
                            name='contact'
                            value={user.contact}
                            onChange={getUserData}
                            placeholder="Contact"
                        />
                        <img src={img2} alt="" />
                    </div>
                    <div className="mb-3 input">
                        <input type="email" className="form-control" id="email"
                            name='email'
                            value={user.email}
                            onChange={getUserData}
                            placeholder="Email"
                        />
                        <img src={img1} alt="" />
                    </div>
                    <div className="mb-3 input">
                        <input type="password" className="form-control" id="pass"
                            name='pass'
                            value={user.pass}
                            onChange={getUserData}
                            placeholder="Password"
                        />
                        <img src={img3} alt="" />
                    </div>
                </form>
                <div className="link">
                <button type="submit" onClick={storingData} className="mybtn" >Sign Up</button>
                    <NavLink to="/login">Already Have An Account ?Log In</NavLink>
                </div>
            </div>
        </div>
    )
}

export default Signup