import React from 'react'
import "./Footer.css"
import img1 from './image 29.svg'
import img2 from './image 30.svg'
import img3 from './image 31.svg'
import { NavLink } from 'react-router-dom'
function Footer() {
    return (
        <div>
            <div className="footer">
                <div className="home">
                    <NavLink to="/allprod">
                    <img src={img1} alt="" />
                    </NavLink>
                    <p>Home</p>
                </div>
                <div className="add">
                    <NavLink to="/additems">
                        <img src={img3} alt="" />
                    </NavLink>
                    <p>Add Items</p>
                </div>
                <div className="account">
                    <NavLink to="/account">
                        <img src={img2} alt="" />
                    </NavLink>
                    <p>Account</p>
                </div>
            </div>
        </div>
    )
}

export default Footer