import React from 'react'
import "./Nav.css"
import img1 from "./Ellipse 4.svg"
import img2 from "./menu.svg"

function Nav() {
  return (
    <div>
        <div className="nav">
            <div className="imgandtext">
                <img src={img1} alt="" />
                <div className="text">
                    <h1>Mr.Rehan</h1>
                    <h2>Admin</h2>
                </div>
            </div>
            <div className="menu">
                <a href="">
            <img src={img2} alt="" />
            </a>
        </div>
        </div>
    </div>
  )
}

export default Nav