import React from 'react'
import img from "./Frame.png"
import "./start.css"
import { BrowserRouter as Router, Routes, Route, NavLink } from "react-router-dom"

function Start() {
    return (
        <div className='main'>
            <div className="first">
                <img src={img} />
                <h1>SAYLANI WELFARE</h1>
                <p>ONLINE DISCOUNT STORE</p>
            </div>
            <div className="second">
            <button type="button" className="startbtn"><NavLink to="/signup">Get Started</NavLink></button>
        </div>
        </div>
    )
}

export default Start