import React from 'react'
import "./account.css"
import img1 from './Ellipse 4.svg'
import img2 from './image 32.svg'
import img3 from './image 13.svg'
import img4 from './image 15.svg'
import img5 from './image 16.svg'
import Footer from '../AdminFooter/Footer'
function Account() {
  return (
    <>
      <div>
        <div className="mainacc">
          <h1>Settings</h1>
          <img src={img1} alt="" />
          <input type="text" placeholder='Update Full Name' />
          <div className="updateacc">
            <img src={img2} alt="" />
          </div>
        </div>
      </div>
      <div className="mainsecond">
        <h1>All Categories</h1>
      </div>
      <div className="fixcat">
        <div className="cardacc">
          <div className="fchild">
            <img src={img3} alt="" />
          </div>
          <div className="schild">
            <h1>Fruits</h1>
          </div>
        </div>
        <div className="cardacc">
          <div className="fchild">
            <img src={img5} alt="" />
          </div>
          <div className="schild">
            <h1>Vegetables</h1>
          </div>
        </div>
        <div className="cardacc">
          <div className="fchild">
            <img src={img4} alt="" />
          </div>
          <div className="schild">
            <h1>Meat</h1>
          </div>
        </div>
        <div className="cardacc">
          <div className="fchild">
            <img src={img3} alt="" />
          </div>
          <div className="schild">
            <h1>Sweets</h1>
          </div>
        </div>
      </div>
      <button className='Logout'>Log Out</button>
      <Footer/>
    </>
  )
}

export default Account