import React from 'react'
import Footer from '../AdminFooter/Footer'
import Nav from '../AdminNav/Nav'
import './Add.css';
import img1 from './image 32.svg'
function AddItem() {
    return (
        <div>
            <Nav />
            <div className="mainadd">
                <h1 className='h1'>All Products</h1>
                <div className="addinputs">
                    <div className="update">
                        <img src={img1} alt="" />
                    </div>
                    <input type="text" placeholder='Item Name' />
                    <input type="text" placeholder='Select Category' />
                    <input id='desc' type="text" placeholder='Describe This Item' />
                </div>
                <div className="unitname">
                    <h1>Unit Name :</h1>
                    <input type="text" placeholder='Pcs. /Kg /Dozen'/>
                </div>
                <div className="unitprice">
                    <h1>Unit Price :</h1>
                    <input type="text" placeholder='PKR 300' />
                </div>
                <button className='addbtn'>Add Product</button>
            </div>
            <Footer />
        </div>
    )
}

export default AddItem