import React, { useState } from 'react'
import "./login.css"
import { useNavigate, NavLink } from 'react-router-dom'
import img1 from "./Email icon.svg"
import img2 from "./Password icon.svg"

function Login() {

    const [user, setuser] = useState({
        pass: "",
        email: "",
    })



    const getUserData = (e) => {
        setuser({ ...user, [e.target.name]: e.target.value })
    }

    const history = useNavigate();
    const storingData = () => {
        history("/home")

    }

    return (
        <div>
            <div className='containerLogin'>
                <div className="first">
                <h1>SAYLANI WELFARE</h1>
                <p>ONLINE DISCOUNT STORE</p>
                </div>
                <form>
                    <div className="mb-3 input">
                        <input type="email" className="form-control" id="email"

                            name='email'
                            value={user.email}
                            onChange={getUserData}
                            placeholder="Email"
                        />
                        <img src={img1} alt="" />
                    </div>
                    <div className="mb-3 input">
                        <input type="password" className="form-control" id="pass"
                            name='pass'
                            value={user.pass}
                            onChange={getUserData}
                            placeholder="Password"
                        />
                        <img src={img2} alt="" />
                    </div>
                </form>
                <div className="link">
                    <button type="submit" onClick={storingData} className="mybtn" >Sign In</button>
                    <NavLink to="/signup">Don't Have An Account ? Register</NavLink>
                </div>
            </div>
        </div>
    )
}

export default Login