import React from 'react'
import Footer from '../AdminFooter/Footer'
import Nav from '../AdminNav/Nav'
import "./AllProd.css"
import img1 from "./image 18.svg"
import img2 from "./image 19.svg"
import img3 from "./image 20.svg"
import img4 from "./image 21.svg"

function AllProd() {
    return (
        <div>
            <Nav/>
            <h1 className='h1'>All Products</h1>
            <div className="cardall">
                <div className="fchild">
                    <img src={img1} alt="" />
                </div>
                <div className="schild">
                    <h1>Apple</h1>
                    <p>1. Kg</p>
                </div>
                <div className="tchild">
                    <p>PKR 200</p>
                </div>
            </div>
            <div className="cardall">
                <div className="fchild">
                    <img src={img2} alt="" />
                </div>
                <div className="schild">
                    <h1>Mango</h1>
                    <p>1. Kg</p>
                </div>
                <div className="tchild">
                    <p>PKR 500</p>
                </div>
            </div>
            <div className="cardall">
                <div className="fchild">
                    <img src={img3} alt="" />
                </div>
                <div className="schild">
                    <h1>Kivi</h1>
                    <p>1. Kg</p>
                </div>
                <div className="tchild">
                    <p>PKR 350</p>
                </div>
            </div>
            <div className="cardall">
                <div className="fchild">
                    <img src={img4} alt="" />
                </div>
                <div className="schild">
                    <h1>Watermelon</h1>
                    <p>1. Kg</p>
                </div>
                <div className="tchild">
                    <p>PKR 300</p>
                </div>
            </div>
        <Footer/>
        </div>
    )
}

export default AllProd